const id = document.getElementById("id");
const btn = document.getElementById("btn");
const ans = document.getElementById("anser");
const arry = ["H", "He", "c"];
const arry2 = ["start", "水素", "ヘリウム", "炭素"];

// ボタンを押したときの処理
// iを初期化
let i = 0;
// ボタンをクリックしたらスタート
btn.addEventListener("click", () => {
  // 問題を表示
  id.innerHTML = arry[i];
  // ボタンの言葉を変更
  if (btn.innerText === "スタート") {
    btn.innerText = "決定";
    ans.classList.add("start");
  } else {
    ans.classList.remove("start");
  }
  // 入力したテキストを取得する
  const text = document.getElementById("text").value;
  // 問題の答え（arry2）の配列に+1をしたものと比べる
  if (text === arry2[i + 1]) {
    // 正解であれば、iを1増やして次の問題を表示
    // テキストをカラに戻す
    ans.innerHTML = "◎";
    i++;
    id.innerHTML = arry[i];
    document.getElementById("text").value = "";
  } else {
    // 不正解であれば、テキストをカラにもどす
    ans.innerHTML = "×";
    document.getElementById("text").value = "";
  }
});

console.log(arry2[i + 1]);
